
.food {
  position: fixed;
  left: 0;
  top: 0;
  bottom: 48px;
  z-index: 30;
  width: 100%;
  background-color: #fff;
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
}
.food .food-content .image-header {
  position: relative;
  width: 100%;
  height: 0;
  padding-top: 100%;
}
.food .food-content .image-header img {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
.food .food-content .image-header .goback {
  position: absolute;
  top: 10px;
  left: 0;
}
.food .food-content .image-header .goback i {
  display: block;
  padding: 10px;
  font-size: 20px;
  color: #fff;
}
.food .food-content .content {
  position: relative;
  padding: 18px;
}
.food .food-content .content h1 {
  line-height: 14px;
  margin-bottom: 8px;
  font-size: 14px;
  font-weight: 700;
  color: #07111b;
}
.food .food-content .content .detail {
  margin-bottom: 18px;
  line-height: 10px;
  font-size: 0;
}
.food .food-content .content .detail .sell-count,
.food .food-content .content .detail .rating {
  font-size: 10px;
  color: #93999f;
}
.food .food-content .content .detail .sell-count {
  margin-right: 12px;
}
.food .food-content .content .price {
  font-weight: 700;
  line-height: 24px;
}
.food .food-content .content .price .now {
  margin-right: 18px;
  font-size: 14px;
  color: #f01414;
}
.food .food-content .content .price .old {
  text-decoration: line-through;
  font-size: 10px;
  color: #93999f;
}
.food .food-content .cartcontrol-warpper {
  position: absolute;
  right: 12px;
  bottom: 12px;
}
.food .food-content .buy {
  position: absolute;
  right: 18px;
  bottom: 18px;
  z-index: 10;
  height: 24px;
  line-height: 24px;
  padding: 0 12px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  font-size: 10px;
  border-radius: 10px;
  color: #fff;
  background-color: #00a0dc;
}
.food .info {
  padding: 18px;
}
.food .info h1 {
  line-height: 14px;
  margin-bottom: 16px;
  font-size: 14px;
  color: #07111b;
}
.food .info .text {
  line-height: 24px;
  padding: 0 8px;
  font-size: 12px;
  color: #4d555d;
}
.fooddetail-enter-active,
.fooddetail-leave {
  -webkit-transform: scale(1);
          transform: scale(1);
}
.fooddetail-leave-active,
.fooddetail-enter {
  -webkit-transform: scale(0);
          transform: scale(0);
}



