
.carcontrol {
  font-size: 0;
}
.carcontrol .car-reduce,
.carcontrol .car-add {
  display: inline-block;
  padding: 6px;
  line-height: 24px;
  font-size: 24px;
  color: #00a0dc;
}
.carcontrol .car-reduce {
  -webkit-transition: all 0.5s;
  transition: all 0.5s;
}
.carcontrol .car-count {
  display: inline-block;
  vertical-align: top;
  width: 12px;
  padding-top: 6px;
  line-height: 24px;
  font-size: 10px;
  color: #93999f;
  -webkit-transition: all 0.5s;
  transition: all 0.5s;
}
.carcontrol .car-add {
  display: inline-block;
}
.move-enter-active,
.move-leave {
  opacity: 1;
  -webkit-transform: translateX(0px) rotateZ(0deg);
          transform: translateX(0px) rotateZ(0deg);
  /*		transform-origin:80% 20%;*/
}
.move-leave-active,
.move-enter {
  opacity: 0;
  -webkit-transform: translateX(24px) rotateZ(180deg);
          transform: translateX(24px) rotateZ(180deg);
  /*		transform-origin:90% 10%;*/
}



